#intro {
    background-image: url('./assets/BG5.jpg');
    /* Full height */
    height: 100%;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
}
#skills {
    background-image: linear-gradient(to bottom right, rgb(102 151 98),rgb(29 31 70));
}
#projects {
    background-image: linear-gradient(to bottom right, rgb(29 31 70), rgb(102 151 98));
}
.full-height {
    min-height: 100vh;
}